/* ====================== HERO - FULLY RESPONSIVE ====================== */
.hero {
  display: flex;
  align-items: center;
  justify-content: flex-start;
  min-height: 100vh;
  min-height: 100dvh;
  padding: 0 clamp(2rem, 6vw, 6rem);
  color: white;
  overflow: hidden;
  gap: clamp(1rem, 4vw, 3rem);
  position: relative;
}

/* ====================== LEFT & RIGHT ====================== */
.hero-left,
.hero-right {
  flex: 1;
  display: flex;
  justify-content: center;
  align-items: center;
}

.hero-left {
  flex: 1;
  margin-left: 0%;
  display: flex;
  justify-content: flex-start;
}

/* ====================== TEXT CONTENT ====================== */
.hero-content {
  max-width: min(90%, 520px);
}

.hero1 { font-size: clamp(1.4rem, 4.5vw, 2.8rem); font-weight: 600; color: #FFD700; margin: 0 0 0.4rem; letter-spacing: 0.5px; }
.hero2 { font-size: clamp(3.5rem, 11vw, 10rem); font-weight: 900; color: #fff; margin: 0; line-height: 0.95; letter-spacing: -1px; }
.hero3 { font-size: clamp(1rem, 2.8vw, 1.6rem); color: #e2e8f0; margin: 0.8rem 0 0; white-space: nowrap; line-height: 1.4; }

/* ====================== RIGHT SIDE ====================== */
.hero-right {
  flex: 1.6;
  min-height: 100vh;
  min-height: 100dvh;
  margin-right: -7%;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 0;
  overflow: visible;
  position: relative;
}

.hero-right > *,
.hero-right canvas {
  width: 100% !important;
  height: 100% !important;
  min-height: 100vh !important;
  min-height: 100dvh !important;
  margin-top: 8%;
  object-fit: contain !important;
  transform: scale(1) translateY(-4%) !important;
  transform-origin: center !important;
}

.mobile-image {
  display: none;
}
/* ====================== LARGE DESKTOPS ====================== */
@media (min-width: 1600px) {
  .hero { padding: 0 8rem; }
  .hero-right > *, .hero-right canvas {
    transform: scale(1.0) translateY(-7%) translateX(-5%) !important;
  }
}

/* ====================== LAPTOPS (1200px â€“ 1599px) ====================== */
@media (max-width: 1599px) and (min-width: 1200px) {
  .hero1 { font-size: clamp(1.6rem, 4vw, 2.3rem); }
  .hero2 { font-size: clamp(4rem, 9vw, 7rem); }
  .hero3 { font-size: clamp(0.9rem, 2.2vw, 1.2rem); }

  .hero-right > *, .hero-right canvas {
    width: 110% !important;
    margin-left: -4% !important;
    transform: scale(0.9) translateY(-10%) translateX(-3.5%) !important;
  }
}

/* ====================== SMALLER LAPTOPS (â‰¤1199px) ====================== */
@media (max-width: 1199px) {
  .hero1 { font-size: clamp(1.4rem, 3.8vw, 2rem); }
  .hero2 { font-size: clamp(3.5rem, 8vw, 6rem); }
  .hero-right > *, .hero-right canvas {
    transform: scale(0.8) translateY(-3%) translateX(-7%) !important;
  }
}

/* ====================== TABLETS & MOBILE ====================== */
@media (max-width: 1024px) {
  .hero {
    flex-direction: column;
    text-align: center;
    padding: 1rem 1.5rem;
    gap: 2rem; }
  .hero-left, .hero-right {
    width: 100%;
    flex: none; }

    .hero-left {
      justify-content: center;
      margin-left: 0;
      margin-top: 20%;
    }
  .hero-content {
    max-width: 100%; }
  .hero3 {
    white-space: normal; }
  .hero-right {
    min-height: 100vh;
    max-height: 100px; }
}

@media (max-width: 768px) {
  .hero1 { font-size: 1.6rem; }
  .hero2 { font-size: 4.5rem; }
  .hero-right {
    min-height: 50vh;
    max-height: 380px;
    display: none !important;
  }
  .hero-left {
    width: 100%;
    justify-content: center;
    margin-top: 10rem;
  }

  .hero-content {
    text-align: center;
  }
}

@media (max-width: 480px) {
  .hero1 { font-size: 1.3rem; }
  .hero2 { font-size: 3.2rem; }
  .hero-right { min-height: 45vh; max-height: 300px; }
}

@media (max-width: 360px) {
  .hero { padding: 1.5rem 1rem; }
  .hero2 { font-size: 2.8rem; }
  .hero-right { min-height: 40vh; max-height: 250px; }
}

@supports (padding: max(0px)) {
  .hero {
    padding-left: max(env(safe-area-inset-left), clamp(2rem, 6vw, 6rem));
    padding-right: max(env(safe-area-inset-right), clamp(2rem, 6vw, 6rem));
  }
}


@media (max-width: 768px) {

  .hero {
    overflow-x: visible;
  }

  .hero-left {
    justify-content: center;
    padding: 0rem; 
  }

  .hero-content {
    display: flex;
    flex-direction: column;
    align-items: center;
  }

  .mobile-image {
    width: 100%;
    max-width: 400px; 
    margin: 0 auto 1rem auto;
    display: block;
  }
}